function Info() {
  let gameCnt = 1;
  let failCnt = 0;
  let playingTime = new Date().getMinutes() + ":" + new Date().getSeconds();

  return (
    <div className="info row">
      <div className="col">
        게임: #{gameCnt}
      </div>
      <div className="col">
        실수: {failCnt} / 3
      </div>
      <div className="col">
        경과시간: {playingTime}
      </div>
    </div>
  );
}

function Board() {
  return (
    <div className="board">
      {/* 9x9 */}
    </div>
  );
}

function Buttons() {
  return (
    <div className="buttons">
      <button>취소</button>
      <button>지우기</button>
      <button>메모 on/off</button>
      <button>게임 시작</button>
    </div>
  );
}

function NumberPad() {
  return (
    <div className="numberPad">
      {/* 1 2 3
          4 5 6
          7 8 9 */}
    </div>
  );
}

function Sudoku(store) {
  return (
    <div className="container-fluid">
        <div className="game">
          <Info store={store}/>
          <Board store={store}/>
          <Buttons store={store}/>
          <NumberPad store={store}/>
        </div>
    </div>
  );
}

export default Sudoku;