import { CANCEL, GAME_OVER, GAME_START, MENO_OFF, MENO_ON, SELECT_NUMBER, UPDATE_NUMBER } from '../actions/index'
import { combineReducers } from 'redux'

const initState = {
  state: "대기",      // "대기", "진행중"
  mode: "입력",       // "입력", "메모"
  failCnt: 0,         // 실수 횟수 (3이면 게임 종료)
  gameCnt: 0,         // 게임 횟수
  playTime: "00:00",  // 게임 진행 시간 (60분이면 게임 종료)
  history: [],        // 번호판에 수정한 내용을 Object형태로 Push
};

const data = (state = initState, action) => {
  switch (action.type) {
    case CANCEL:
      const lastAction = state.history.pop();    
      // TODO : state.history 에서 마지막꺼 꺼내서 동작 되돌리기

      return Object.assign({}, state, {
        history: state.history
      });
    case GAME_OVER:
      return Object.assign({}, state, {
        state: "대기",
      });
    case GAME_START:
      return Object.assign({}, state, {
        state: "진행중",
        mode: "입력",
        failCnt: 0,
        gameCnt: state.gameCnt + 1,
        playTime: "00:00",
        history: [],
      });
    case MENO_OFF:
      return Object.assign({}, state, {
        mode: "입력",
      });
    case MENO_ON:
      return Object.assign({}, state, {
        mode: "메모",
      });
    case SELECT_NUMBER:
      return Object.assign({}, state, {
        number: state.number - 1
      });
    case UPDATE_NUMBER:
      return Object.assign({}, state, {
        number: state.number - 1
      });
    default:
      return state;
  }
};

const Reducer = combineReducers({
  data
});

export default Reducer;