export const GAME_START = 'GAME_START';
export const GAME_OVER = 'GAME_OVER';

export const SELECT_NUMBER = 'SELECT_NUMBER';
export const UPDATE_NUMBER = 'UPDATE_NUMBER';

export const MENO_ON = 'MENO_ON';
export const MENO_OFF = 'MENO_OFF';

export const CANCEL = 'CANCEL';

export const game_start = () => {
    return {
        type: GAME_START
    }
};
export const game_over = () => {
    return {
        type: GAME_OVER
    }
};

export const select_number = () => {
  return {
      type: SELECT_NUMBER
  }
};
export const update_number = () => {
  return {
      type: UPDATE_NUMBER
  }
};

export const meno_on = () => {
  return {
      type: MENO_ON
  }
};
export const meno_off = () => {
  return {
      type: MENO_OFF
  }
};

export const cancel = () => {
  return {
      type: CANCEL
  }
};