import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

import 'jquery';
import 'bootstrap/dist/js/bootstrap.bundle';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';

import Header from './components/Header';
import Dashboard from './components/Dashboard';
import MyMap from './components/MyMap';
import Board from './components/Board';
import Blog from './components/Blog';
import Sns from './components/Sns';
import Footer from './components/Footer';

function App(props) {
  return (
    <div className="App">
      <Provider store={props.store}>
        <Router>
          <Header />
          <Switch>
            <Route exact path='/my_react' component={Dashboard} />
            <Route exact path='/my_react/myMap' component={MyMap} />
            <Route exact path='/my_react/board' component={Board} />
            <Route exact path='/my_react/blog' component={Blog} />
            <Route exact path='/my_react/sns' component={Sns} />
          </Switch>
        </Router>
        <Footer />
      </Provider>
    </div>
  );
}

export default App;
